#heading {
    color: var(--text-color);
    width: 100%;
    padding: 10px;
    margin: auto;
}

#heading h1 {
    padding-left: 10px;
}


/* Media Queries */
/* Apply to 600px and larger */
@media only screen and (min-width: 600px) {
    #content {
        width: 70%;
        margin: auto;
    }

    .columns {
        display: flex;
        flex-direction: row;
    }

    .one {
        flex: 1;
    }

    .two {
        flex: 2;
    }

    .three {
        flex: 3;
    }

    .four {
        flex: 4;
    }
}